import React from "react";
import "./Widgetsm.css";
import { Visibility } from "@material-ui/icons";

const Widgetsm = () => {
  return (
    <div className="small">
      <h3 className="widgetsmTitle">کاربران جدید</h3>
      <div className="widgetsmUser">
        <img src="https://randomuser.me/api/portraits/women/34.jpg" alt="" />
        <ul className="widgetsmList">
          <li className="widgetsmListItem">مونا شریفی</li>
        </ul>
        <div className="btnDisplay">
          <Visibility />
          <span className="txtDisplay">Display</span>
        </div>
      </div>
      <div className="widgetsmUser">
        <img src="https://randomuser.me/api/portraits/women/34.jpg" alt="" />
        <ul className="widgetsmList">
          <li className="widgetsmListItem">حسن قدیری</li>
        </ul>
        <div className="btnDisplay">
          <Visibility />
          <span className="txtDisplay">Display</span>
        </div>
      </div>
      <div className="widgetsmUser">
        <img src="https://randomuser.me/api/portraits/women/34.jpg" alt="" />
        <ul className="widgetsmList">
          <li className="widgetsmListItem">کریم رضایی</li>
        </ul>
        <div className="btnDisplay">
          <Visibility />
          <span className="txtDisplay">Display</span>
        </div>
      </div>
    </div>
  );
};

export default Widgetsm;
