import React from "react";
import "./topbar.css";
import { NotificationsNone, Language, Settings } from "@material-ui/icons";

const Topbar = () => {
  return (
    <div className="topbar">
      <div className="topbarWrapper">
        <div className="left">
          <div className="iconTopbarContainer">
            <NotificationsNone />
            <span className="numberbadge yellow">2</span>
          </div>
          <div className="iconTopbarContainer">
            <Language />
            <span className="numberbadge">2</span>
          </div>
          <div className="iconTopbarContainer">
            <Settings />
          </div>
          <div>
            <img
              src="https://randomuser.me/api/portraits/women/34.jpg"
              alt=""
              className="avatar"
            />
          </div>
        </div>
        <div className="right">
          <span className="logo">پنل کاربری</span>
        </div>
      </div>
    </div>
  );
};

export default Topbar;
