import React from "react";
import "./sidebar.css";
import {
  Home,
  Timeline,
  AddToQueue,
  ShowChart,
  Assessment,
  Receipt,
  Mail,
  Message,
  Feedback,
  AttachMoney,
  Person,
  Report,
  Whatshot,
} from "@material-ui/icons";
import { Link } from "react-router-dom";

const Sidebar = (props) => {
  // const isActive = (route)=>{
  //   return route === props.match.path
  // }

  return (
    <div className="sidebar">
      <div className="sidebarWrapper">
        <div className="sidebarMenu">
          <h3 className="sidebarTitle">داشبورد</h3>
          <ul className="sidebarList">
            <Link to="/" className="link">
              <li className="sidebarListItem">
                <Home className="sidebarIcon" />
                خانه
              </li>
            </Link>
            <li className="sidebarListItem">
              <Timeline className="sidebarIcon" />
              آنالیز
            </li>
            <li className="sidebarListItem">
              <AttachMoney className="sidebarIcon" />
              فروش
            </li>
          </ul>
        </div>
        <div className="sidebarMenu">
          <h3 className="sidebarTitle">دسترسی سریع</h3>
          <ul className="sidebarList">
            <Link to="/users" className="link">
              <li className="sidebarListItem">
                <Person className="sidebarIcon" />
                کاربران
              </li>
            </Link>
            <Link to="/products" className="link">
              <li className="sidebarListItem">
                <Whatshot className="sidebarIcon" />
                محصولات
              </li>
            </Link>
            <li className="sidebarListItem">
              <Receipt className="sidebarIcon" />
              تراکنش ها
            </li>
            <li className="sidebarListItem">
              <ShowChart className="sidebarIcon" />
              آمار
            </li>
          </ul>
        </div>
        <div className="sidebarMenu">
          <h3 className="sidebarTitle">هشدار ها</h3>
          <ul className="sidebarList">
            <li className="sidebarListItem">
              <Mail className="sidebarIcon" />
              ایمیل ها
            </li>
            <li className="sidebarListItem">
              <Feedback className="sidebarIcon" />
              بازخورد ها
            </li>
            <li className="sidebarListItem">
              <Message className="sidebarIcon" />
              پیام ها
            </li>
          </ul>
        </div>
        <div className="sidebarMenu">
          <h3 className="sidebarTitle">افراد</h3>
          <ul className="sidebarList">
            <li className="sidebarListItem">
              <AddToQueue className="sidebarIcon" />
              مدیریت
            </li>
            <li className="sidebarListItem">
              <Assessment className="sidebarIcon" />
              آنالیز
            </li>
            <li className="sidebarListItem">
              <Report className="sidebarIcon" />
              گزارشات تخلف
            </li>
          </ul>
        </div>
      </div>
    </div>
  );
};

export default Sidebar;
