import React from "react";
import "./Widgetlg.css";

const Widgetlg = () => {
  const Button = ({ type, text }) => {
    return <button className={`Button ${type}`}>{text}</button>;
  };

  return (
    <div className="large">
      <h3 className="widgetlargeTitle">آخرین تراکنش ها</h3>
      <tabel className="widgetTabelLarge">
        <tr className="widgetLatgeTr">
          <th className="widgetLatgeTh">وضعیت</th>
          <th className="widgetLatgeTh">مقدار</th>
          <th className="widgetLatgeTh">تاریخ</th>
          <th className="widgetLatgeTh">مشتری</th>
        </tr>
        <tr className="widgetLatgeTr">
          <td className="widgetLatgeTd">
            <Button type="approved" text="تایید شده" />
          </td>

          <td className="widgetLatgeTd">6000تومان</td>
          <td className="widgetLatgeTd">17 شهریور</td>
          <td className="widgetLatgeTd">رضا صالحی</td>
        </tr>
        <tr className="widgetLatgeTr">
          <td className="widgetLatgeTd">
            <Button type="approved" text="تایید شده" />
          </td>

          <td className="widgetLatgeTd">6000تومان</td>
          <td className="widgetLatgeTd">17 شهریور</td>
          <td className="widgetLatgeTd">رضا صالحی</td>
        </tr>
        <tr className="widgetLatgeTr">
          <td className="widgetLatgeTd">
            <Button type="cancel" text="رد شده" />
          </td>

          <td className="widgetLatgeTd">6000تومان</td>
          <td className="widgetLatgeTd">17 شهریور</td>
          <td className="widgetLatgeTd">رضا صالحی</td>
        </tr>
      </tabel>
    </div>
  );
};

export default Widgetlg;
