import {
  CalendarToday,
  LocationSearching,
  MailOutline,
  PermIdentity,
  PhoneAndroid,
  Publish,
} from "@material-ui/icons";
import { Link } from "react-router-dom";
import "./user.css";

export default function User() {
  return (
    <div className="user">
      <div className="userTitleContainer">
        <Link to="/newUser">
          <button className="userAddButton">ایجاد کاربر جدید</button>
        </Link>
        <h1 className="userTitle">ویرایش کاربر</h1>
      </div>
      <div className="userContainer">
        <div className="userUpdate">
          <span className="userUpdateTitle">ویرایش</span>
          <form className="userUpdateForm">
            <div className="userUpdateLeft">
              <div className="userUpdateItem">
                <label>نام کاربری</label>
                <input
                  type="text"
                  placeholder="mahanz123"
                  className="userUpdateInput"
                />
              </div>
              <div className="userUpdateItem">
                <label>نام و نام خانوادگی</label>
                <input
                  type="text"
                  placeholder="مهناز افشار"
                  className="userUpdateInput"
                />
              </div>
              <div className="userUpdateItem">
                <label>ایمیل</label>
                <input
                  type="text"
                  placeholder="mahnaz@gmail.com"
                  className="userUpdateInput"
                />
              </div>
              <div className="userUpdateItem">
                <label>َهمراه</label>
                <input
                  type="text"
                  placeholder="09123456789"
                  className="userUpdateInput"
                />
              </div>
              <div className="userUpdateItem">
                <label>آدرس</label>
                <input
                  type="text"
                  placeholder="ایران | تهران"
                  className="userUpdateInput"
                />
              </div>
            </div>
            <div className="userUpdateRight">
              <div className="userUpdateUpload">
                <img
                  className="userUpdateImg"
                  src="https://randomuser.me/api/portraits/women/34.jpg"
                  alt=""
                />
                <label htmlFor="file">
                  <Publish className="userUpdateIcon" />
                </label>
                <input type="file" id="file" style={{ display: "none" }} />
              </div>
              <button className="userUpdateButton">Update</button>
            </div>
          </form>
        </div>
        <div className="userShow">
          <div className="userShowTop">
            <img
              src="https://randomuser.me/api/portraits/women/34.jpg"
              alt=""
              className="userShowImg"
            />
            <div className="userShowTopTitle">
              <span className="userShowUsername">مهناز افشار</span>
              <span className="userShowUserTitle">Industrial Engineer</span>
            </div>
          </div>
          <div className="userShowBottom">
            <span className="userShowTitle">جزئیات اکانت</span>
            <div className="userShowInfo">
              <PermIdentity className="userShowIcon" />
              <span className="userShowInfoTitle">mahanz123</span>
            </div>
            <div className="userShowInfo">
              <CalendarToday className="userShowIcon" />
              <span className="userShowInfoTitle">21 شهریور</span>
            </div>
            <span className="userShowTitle">مشخصات</span>
            <div className="userShowInfo">
              <PhoneAndroid className="userShowIcon" />
              <span className="userShowInfoTitle">09123456789</span>
            </div>
            <div className="userShowInfo">
              <MailOutline className="userShowIcon" />
              <span className="userShowInfoTitle">mahnaz@gmail.com</span>
            </div>
            <div className="userShowInfo">
              <LocationSearching className="userShowIcon" />
              <span className="userShowInfoTitle">ایرن | تهران</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
