import "./Newuser.css";

export default function NewUser() {
  return (
    <div className="newUser">
      <h1 className="newUserTitle">کاربر جدید</h1>
      <form className="newUserForm">
        <div className="newUserItem">
          <label>نام کاربری</label>
          <input type="text" placeholder="رضا" />
        </div>
        <div className="newUserItem">
          <label>نام خانوادگی</label>
          <input type="text" placeholder="صالحی" />
        </div>
        <div className="newUserItem">
          <label>ایمیل</label>
          <input type="email" placeholder="reza@gmail.com" />
        </div>
        <div className="newUserItem">
          <label>رمز عبور</label>
          <input type="password" placeholder="123456789" />
        </div>
        <div className="newUserItem">
          <label>همراه</label>
          <input type="text" placeholder="09123456789" />
        </div>
        <div className="newUserItem">
          <label>آدرس</label>
          <input type="text" placeholder="ایران | تهران" />
        </div>
        <div className="newUserItem">
          <label>جنسیت</label>
          <div className="newUserGender">
            <input type="radio" name="gender" id="male" value="male" />
            <label for="male">مرد</label>
            <input type="radio" name="gender" id="female" value="female" />
            <label for="female">زن</label>
            <input type="radio" name="gender" id="other" value="other" />
            <label for="other">سایر</label>
          </div>
        </div>
        <div className="newUserItem">
          <label>وضعیت(فعال)</label>
          <select className="newUserSelect" name="active" id="active">
            <option value="yes">بله</option>
            <option value="no">خیر</option>
          </select>
        </div>
        <button className="newUserButton">ایجاد کاربر جدید</button>
      </form>
    </div>
  );
}
