import React from "react";
import "./userList.css";
import { DataGrid } from "@material-ui/data-grid";
import { DeleteOutline } from "@material-ui/icons";
import { userRows } from "../../damyData";
import { Link } from "react-router-dom";
import { useState } from "react";

const UserList = () => {
  const [data, setData] = useState(userRows);

  const handleRemove = (id) => {
    const newDetail = data.filter((user) => user.id !== id);
    setData(newDetail);
  };

  const columns = [
    { field: "id", آیدی: "ID", width: 120 },
    {
      field: "username",
      headerName: "نام کاربری",
      width: 190,
      renderCell: (params) => {
        return (
          <div className="userListUser">
            <img src={params.row.avatar} alt="" className="userList" />
            {params.row.username}
          </div>
        );
      },
    },
    {
      field: "email",
      headerName: "ایمیل",
      width: 190,
    },
    {
      field: "status",
      headerName: "وضعیت",
      width: 190,
    },
    {
      field: "transaction",
      headerName: "تراکنش ها",
      width: 190,
    },
    {
      field: "action",
      headerName: "عملیات",
      width: 190,
      renderCell: (params) => {
        console.log(params);
        return (
          <>
            <Link to={`/user/${params.row.id}`}>
              <button className="btnEdit">ویرایش</button>
            </Link>
            <DeleteOutline
              className="btnDelete"
              onClick={() => handleRemove(params.row.id)}
            />
          </>
        );
      },
    },
  ];

  return (
    <div className="userList">
      <DataGrid
        rows={data}
        columns={columns}
        pageSize={8}
        checkboxSelection
        disableSelectionOnClick
      />
    </div>
  );
};

export default UserList;
